"use client";

import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from "recharts";

const COLORS = ["#4ade80", "#22c55e", "#16a34a", "#15803d", "#166534"];

export default function IncomePieChart({ transactions }) {
  // Filter only deposits
  const incomeTx = transactions.filter(t => t.deposit > 0);

  // Group income by remarks/category
  const grouped = {};
  incomeTx.forEach(t => {
    const key = t.remarks?.trim() || "Uncategorized";
    grouped[key] = (grouped[key] || 0) + t.deposit;
  });

  const chartData = Object.keys(grouped).map(key => ({
    name: key,
    value: grouped[key],
  }));

  return (
    <div className="w-full h-[300px]">
      <h2 className="font-semibold text-lg mb-2">Income Breakdown</h2>
      <ResponsiveContainer>
        <PieChart>
          <Pie
            data={chartData}
            cx="50%"
            cy="50%"
            label
            outerRadius={110}
            dataKey="value"
          >
            {chartData.map((_, i) => (
              <Cell key={i} fill={COLORS[i % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip />
          <Legend />
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}
